import React from "react";
import "./Mail.css";
import { useNavigate } from "react-router-dom";
import { IconButton } from "@mui/material";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import MoveToInboxIcon from "@mui/icons-material/MoveToInbox";
import ErrorIcon from "@mui/icons-material/Error";
import DeleteIcon from "@mui/icons-material/Delete";
import EmailIcon from "@mui/icons-material/Email";
import AccessTimeFilledIcon from "@mui/icons-material/AccessTimeFilled";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import LabelImportantIcon from "@mui/icons-material/LabelImportant";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import UnfoldMoreIcon from "@mui/icons-material/UnfoldMore";
import PrintIcon from "@mui/icons-material/Print";
import ExitToAppIcon from "@mui/icons-material/ExitToApp";
import { useSelector } from "react-redux";
import { db } from "../../firebase/firebase";
import { deleteDoc, doc } from "firebase/firestore";
function Mail() {
  const navigate = useNavigate();
  const selected = useSelector((state) => state.mail.selected)?.payload;
  // console.log(selected);
  function deleteMail() {
    let id = selected.id;
    deleteDoc(doc(db, "emails", id));
    navigate("/", { replace: false });
    alert("Deleted");
  }
  return (
    <div className="mail-section">
      <div className="top-widgets">
        <div className="left">
          <IconButton onClick={() => navigate("/", { replace: false })}>
            <ArrowBackIcon></ArrowBackIcon>
          </IconButton>
          <IconButton>
            <MoveToInboxIcon></MoveToInboxIcon>
          </IconButton>
          <IconButton>
            <ErrorIcon></ErrorIcon>
          </IconButton>
          <IconButton onClick={deleteMail}>
            <DeleteIcon></DeleteIcon>
          </IconButton>
          <IconButton>
            <EmailIcon></EmailIcon>
          </IconButton>
          <IconButton>
            <AccessTimeFilledIcon></AccessTimeFilledIcon>
          </IconButton>
          <IconButton>
            <CheckCircleIcon></CheckCircleIcon>
          </IconButton>
          <IconButton>
            <LabelImportantIcon></LabelImportantIcon>
          </IconButton>
          <IconButton>
            <MoreVertIcon></MoreVertIcon>
          </IconButton>
        </div>
        <div className="right">
          <IconButton>
            <UnfoldMoreIcon></UnfoldMoreIcon>
          </IconButton>
          <IconButton>
            <PrintIcon></PrintIcon>
          </IconButton>
          <IconButton>
            <ExitToAppIcon></ExitToAppIcon>
          </IconButton>
        </div>
      </div>
      <div className="container">
        <div className="content">
          <div className="header">
            <h3>{selected.title}</h3>
            <LabelImportantIcon className="important"></LabelImportantIcon>
            <p>{selected.from}</p>
            <p className="date">{selected.date}</p>
          </div>
          <div className="message">{selected.body}</div>
        </div>
      </div>
    </div>
  );
}

export default Mail;
