import React, { useEffect } from "react";
import "./App.css";
import Header from "./Components/Header/Header";
import RightSide from "./Components/RightSide/RightSide";
import { Route, Routes, useNavigate, Link } from "react-router-dom";
import MiddlePart from "./Components/MiddlePart/MiddlePart";
import Mail from "./Components/Mail/Mail";
import SendMail from "./Components/SendMail/SendMail";
import { useDispatch, useSelector } from "react-redux";
import Login from "./Components/Login/Login";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { changeEmail, editPhoto, login } from "./features/userSlice";
import { collection, onSnapshot } from "firebase/firestore";
import { db } from "./firebase/firebase";
import { updateList } from "./features/mailSlice";
function App() {
  const sendMailOpen = useSelector((state) => state.mail.sendMessage);
  const logedIn = useSelector((state) => state.user.user);
  const email = useSelector((state) => state.user.email)?.payload;
  const dispatch = useDispatch();
  console.log(email);
  const navigate = useNavigate();
  const auth = getAuth();
  // if (logedIn) {
  //   navigate("/", { replace: false });
  // }
  async function getDataFromFireStore() {
    onSnapshot(collection(db, "emails"), (snapshot) => {
      const arr = [];
      snapshot.docs.forEach((doc) => {
        console.log(email, doc.data().to);
        if (email === doc.data().to) {
          const obj = { id: doc.id, ...doc.data() };
          // console.log(obj);
          arr.push(obj);
        }
      });
      dispatch(updateList(arr));
    });
  }

  useEffect(() => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        dispatch(login(user.accessToken));
        dispatch(editPhoto(user.photoURL));
        dispatch(changeEmail(user.email));
        navigate("/", { replace: false });
      }
    });
    getDataFromFireStore();
    if (!logedIn) {
      navigate("/login", { replace: false });
    }
  }, [auth, email]);
  return (
    <div className="App">
      <div className="content">
        {!logedIn && (
          <Routes>
            <Route
              path="/"
              element={
                <div style={{ fontSize: "30px", textAlign: "center" }}>
                  <p>please sign in first</p>
                  <Link to="/login">Click Here</Link>
                </div>
              }
            ></Route>
            <Route path="/login" element={<Login />}></Route>
          </Routes>
        )}
        {logedIn && (
          <>
            <Header></Header>
            <div className="mid-page">
              <RightSide></RightSide>
              <Routes>
                <Route path="/" element={<MiddlePart />}></Route>
                <Route path="/mail" element={<Mail />}></Route>
              </Routes>
            </div>
            {sendMailOpen && <SendMail></SendMail>}
          </>
        )}
      </div>
    </div>
  );
}

export default App;
