import React, { useState } from "react";
import "./EmailSection.css";
import { Checkbox, IconButton } from "@mui/material";
import StarBorderIcon from "@mui/icons-material/StarBorder";
import DoubleArrowIcon from "@mui/icons-material/DoubleArrow";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { changeSelected } from "../../features/mailSlice";
import { doc, updateDoc } from "firebase/firestore";
import { db } from "../../firebase/firebase";
function EmailSection({ from, title, body, date, id, stared, important }) {
  const [theStared, setTheStared] = useState(stared);
  const [theImportant, setTheImportant] = useState(important);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  function selectHandler() {
    dispatch(changeSelected({ from, title, body, date, id }));
  }
  function starHandler() {
    if (theStared) {
      updateDoc(doc(db, "emails", id), { stared: false });
      return;
    }
    updateDoc(doc(db, "emails", id), { stared: true });
  }
  function importantHandler() {
    if (theImportant) {
      updateDoc(doc(db, "emails", id), { important: false });
      return;
    }
    updateDoc(doc(db, "emails", id), { important: true });
  }
  return (
    <div className="single-email" onClick={selectHandler}>
      <div>
        <Checkbox></Checkbox>
        <IconButton
          onClick={starHandler}
          style={{ color: theStared ? "#e7da39" : "" }}
        >
          <StarBorderIcon></StarBorderIcon>
        </IconButton>
        <IconButton
          onClick={importantHandler}
          style={{ color: theImportant ? "#e7da39" : "" }}
        >
          <DoubleArrowIcon></DoubleArrowIcon>
        </IconButton>
      </div>
      <div
        className="info"
        onClick={() => navigate("/mail", { replace: false })}
      >
        <p className="from">{from}</p>
        <p className="title">{title}</p>
        <p className="body">- {body}</p>
        <p className="date">{date}</p>
      </div>
    </div>
  );
}

export default EmailSection;
