import React from "react";
import "./MiddleWidget.css";
function MiddleWidget({ Icon, text, color, selected }) {
  return (
    <div
      className="single-widget"
      style={{
        color: selected ? color : "gray",
        borderBottom: selected ? `2px solid ${color}` : null,
        backgroundColor: selected ? "rgba(128, 128, 128, 0.116)" : null,
      }}
    >
      <Icon></Icon>
      <p>{text}</p>
    </div>
  );
}

export default MiddleWidget;
