import React from "react";
import "./RightOption.css";
function RightOption({ Icon, text, number, selected }) {
  return (
    <div className={`right-option ${selected ? "active" : ""}`}>
      <div>
        <Icon></Icon>
        <p>{text}</p>
      </div>
      <p className="number">{number}</p>
    </div>
  );
}

export default RightOption;
