import React from "react";
import "./RightSide.css";
import RightOption from "./RightOption";
import InboxIcon from "@mui/icons-material/Inbox";
import StarIcon from "@mui/icons-material/Star";
import AccessTimeIcon from "@mui/icons-material/AccessTime";
import DoubleArrowIcon from "@mui/icons-material/DoubleArrow";
import SendIcon from "@mui/icons-material/Send";
import FolderIcon from "@mui/icons-material/Folder";
import { Button, IconButton } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import PersonIcon from "@mui/icons-material/Person";
import DuoIcon from "@mui/icons-material/Duo";
import CallIcon from "@mui/icons-material/Call";
import { useDispatch, useSelector } from "react-redux";
import { open } from "../../features/mailSlice";
function RightSide() {
  const dispatch = useDispatch();
  const emailList = useSelector((state) => state.mail.emailList)?.payload;
  console.log(emailList);
  const staredList = emailList?.filter((email) => email.stared);
  const importantList = emailList?.filter((email) => email.important);
  // console.log(staredList);
  return (
    <div className="right-side">
      <Button
        startIcon={<AddIcon />}
        className="compose"
        onClick={() => dispatch(open())}
      >
        Compose
      </Button>
      <div className="options">
        <RightOption
          Icon={InboxIcon}
          text="Inbox"
          selected={true}
          number={emailList?.length}
        ></RightOption>
        <RightOption
          Icon={StarIcon}
          text="Stared"
          number={staredList?.length}
        ></RightOption>
        <RightOption
          Icon={DoubleArrowIcon}
          text="Important"
          number={importantList?.length}
        ></RightOption>
        <RightOption
          Icon={AccessTimeIcon}
          selected={false}
          text="Snoozed"
          number={null}
        ></RightOption>
        <RightOption Icon={SendIcon} text="Sent" number={null}></RightOption>
        <RightOption
          Icon={FolderIcon}
          text="Drafts"
          number={null}
        ></RightOption>
        <RightOption
          Icon={ExpandMoreIcon}
          text="More"
          number={null}
        ></RightOption>
      </div>
      <div className="right-side-footer">
        <IconButton>
          <PersonIcon></PersonIcon>
        </IconButton>
        <IconButton>
          <DuoIcon></DuoIcon>
        </IconButton>
        <IconButton>
          <CallIcon></CallIcon>
        </IconButton>
      </div>
    </div>
  );
}

export default RightSide;
