import React, { useState } from "react";
import "./SendMail.css";
import { useDispatch, useSelector } from "react-redux";
import { open } from "../../features/mailSlice";
import { db } from "../../firebase/firebase";
import { collection, addDoc, serverTimestamp } from "firebase/firestore";
function SendMail() {
  const dispatch = useDispatch();
  const [to, setTo] = useState("");
  const [toError, setToError] = useState(false);
  const [subject, setSubject] = useState("");
  const [subjectError, setSubjectError] = useState(false);
  const [message, setMessage] = useState("");
  const [messageError, setMessageError] = useState(false);
  let from = useSelector((state) => state.user.email)?.payload;
  console.log(from);
  function validateForm(e, selector) {
    if (selector === "To") {
      setTo(e.target.value);
      if (to !== "") {
        setToError(false);
      }
    } else if (selector === "Subject") {
      setSubject(e.target.value);
      if (subject !== "") {
        setSubjectError(false);
      }
    } else {
      setMessage(e.target.value);
      if (message !== "") {
        setMessageError(false);
      }
    }
    // console.log(to, subject, message);
  }
  function formSubmit(e) {
    e.preventDefault();
    let err = false;
    if (!to.includes("@")) {
      setToError(true);
      err = true;
    }
    if (subject === "") {
      setSubjectError(true);
      err = true;
    }
    if (message === "") {
      setMessageError(true);
      err = true;
    }
    if (err) {
      return;
    }
    console.log(to, subject, message, from);
    addDoc(collection(db, "emails"), {
      to,
      subject,
      message,
      from,
      timestamp: new Date().toString(),
    });
  }
  return (
    <div className="send-mail">
      <p>Send Message</p>
      <form onSubmit={formSubmit}>
        <input
          placeholder="To"
          type="email"
          onChange={(e) => validateForm(e, "To")}
        />
        {toError && (
          <p className="error">
            Somthing Wrong in (To) Field (Sould Contain @)
          </p>
        )}
        <input
          placeholder="Subject"
          type="text"
          onChange={(e) => validateForm(e, "Subject")}
        />
        {subjectError && <p className="error">Please Fill the Subject Input</p>}
        {/* <input placeholder="content" /> */}
        <textarea
          placeholder="Message"
          onChange={(e) => validateForm(e, "Message")}
        ></textarea>
        {messageError && <p className="error">Please Fill the Message Input</p>}
        <div>
          <button type="submit">Send</button>
        </div>
      </form>
      <span onClick={() => dispatch(open())}>X</span>
    </div>
  );
}

export default SendMail;
